import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'

// NProgress.configure({ showSpinner: true });

Vue.use(ElementUI)

Vue.config.productionTip = false

// 自动化全局注册组件
// 详细参考https://cn.vuejs.org/v2/guide/components-registration.html#基础组件的自动化全局注册
// 从某个文件夹中按照某个正则表达式查找.vue文件，返回值是一个函数
// require.context是webpack提供的，返回webpackContext，是构造函数，webpack前端流行的构建工具。
const requireComponent = require.context(
  // 其组件目录的相对路径
  './components',
  // 是否查询其子目录
  false,
  // 匹配基础组件文件名的正则表达式
  /Music[A-Z]\w+\.(vue|js)$/
)
// console.log(requireComponent);
// console.log(requireComponent.keys());
requireComponent.keys().forEach(fileName => {
  // 获取组件配置
  const componentConfig = requireComponent(fileName)
  // 获取组件的名称
  const componentName = fileName.split('/').pop().replace(/\.\w+$/, '');
  // console.log(componentName);
  // console.log( componentConfig.default)
  // 全局注册组件
  Vue.component(
    componentName,
    // 如果这个组件选项是通过 `export default` 导出的，
    // 那么就会优先使用 `.default`，
    // 否则回退到使用模块的根。
    componentConfig.default || componentConfig
  )
})

// 全局前置守卫
router.beforeEach((to,from,next)=>{
  NProgress.start();
  next();
})

router.afterEach((to,from)=>{
  NProgress.done();
})


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
