<template>
  <div class="live">
    <div class="title">
      <h2>相关直播推荐</h2>
      <span>你已经被我一个人包围</span>
    </div>
    <div class="live-list clearfix">
      <router-link
        class="room"
        target="_blank"
        :to="'/live/' + l.rid"
        v-for="(l, i) in liveList"
        :key="i"
      >
        <div class="logo">
          <img :src="l.liveimg" alt="" />
          <span class="status"><i></i>直播中</span>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      liveList: [],
    };
  },
  created() {
    this.getLive();
  },
  methods: {
    getLive() {
      var url = "https://music.taihe.com/v1/musicshow/livelist";
      axios.get(url).then((res) => {
        this.liveList = res.data.data.result.slice(0, 4);
      });
    },
  },
};
</script>

<style scoped>
.live .title h2 {
  display: inline-block;
  vertical-align: middle;
  font-size: 28px;
  font-weight: 500;
  color: #000;
  margin-right: 20px;
}

.live .title span {
  color: #909399;
  font-size: 16px;
}
.live-list {
  margin-top: 20px;
}
.live-list a.room {
  float: left;
  width: calc((1200px - 60px) / 4);
  margin-right: 20px;
}
.live-list a.room:last-of-type {
  margin-right: 0;
}
.live-list a.room .logo {
  display: block;
  position: relative;
  height: 180px;
  overflow: hidden;
}
.live-list a.room .logo img {
  transition: all 0.5s ease-out;
  width: 100%;
}
.live-list a.room .logo:hover img {
  transform: scale(1.05);
}
.live-list a.room .logo .status {
  position: absolute;
  left: 16px;
  top: 16px;
  color: #fff;
  font-size: 12px;
  width: 66px;
  height: 24px;
  line-height: 24px;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 15px;
  text-align: center;
}
.live-list a.room .logo .status i {
  display: inline-block;
  vertical-align: middle;
  width: 6px;
  height: 6px;
  background: #e61723;
  border-radius: 100%;
  margin-right: 4px;
}
</style>